	"use strict"
	var self_
	import sectionOne from './components/sectionOne';
	import sectionTwo from './components/sectionTwo';
	import sectionThree from './components/sectionThree';
	import sectionFour from './components/sectionFour';
	import sectionFive from './components/sectionFive';
	import sectionSix from './components/sectionSix';
	import { testOneTest } from '@/api/todoChild/loan.js';
	import { LOAN_TESTONETEST_SUBMIT} from '@/config/router.js';
	export default {
		components:{
				  sectionOne,
				  sectionTwo,
				  sectionThree,
				  sectionFour,
				  sectionFive,
				  sectionSix,
		},
	  data () {
	    return {
	       scrollindex:0,  //当前页面的索引值
	          totalnum:6,  //总共页面数
	          starty:0,  //开始的位置x
	          endy:0, //结束的位置y
	          critical: 100, //触发翻页的临界值
	          margintop:0,  //滑动下拉距离
			  transform:'translateY(-' + 0 + '%)',
			  dataList:{},
			  
	    }
	  },
	  methods:{
		  test(e,v){
			  console.log(e)
			  switch(v) {
			  	case 1:  
				 self_.dataList.one ={
    "titleid": "1",
    "chooseid": e
}
				 return ;
			  	case 2:
				self_.dataList.two = {
				 "titleid": "2",
				 "chooseid": e}
				 return ;
			  	case 3: 
				self_.dataList.three = {
				"titleid": "3",
				"chooseid": e}
				return ;
			  	case 4: 
				self_.dataList.four ={
				"titleid": "4",
				"chooseid": e}
				return ;
				case 5:
				 self_.dataList.five ={
				 "titleid": "5",
				 "chooseid": e}
				 return ;
				case 6:
				 self_.dataList.six ={
				 "titleid": "6",
				 "chooseid": e}
				 return ;
				 case 7:
				  self_.dataList.seven ={
				  "titleid": "7",
				  "chooseid": e}
				  return ;
				  case 8:
				   self_.dataList.eight ={
				   "titleid": "8",
				   "chooseid": e}
				   return ;
			  }
			  
		  },
		 async submit(){
			 for(let key  in this.dataList){
				 if(this.dataList[key]){
					 await testOneTest(this.dataList[key])
				 }
			         // console.log(key + '---' + obj[key])
			   }
			  
			 
			  uni.showToast({
			  								title: "提交成功",
			  								icon: 'success',
			  								duration: 2000,
			  							});
			  							setTimeout(()=>{
			  											uni.navigateTo({
			  												url:LOAN_TESTONETEST_SUBMIT
			  											})
			  										},2000)
			  
		  },
		  scrollTouchstart:function(e){
			  
		      let py = e.touches[0].pageY;
			  // console.log(py)
			  self_.starty = py
		    },
		    scrollTouchmove:function(e){
		      let py = e.touches[0].pageY;
			  // console.log(py)
		      let d = this;
			  self_.endy =py;
		      if(py-d.starty<100 && py-d.starty>-100){   
				  self_.margintop=py - d.starty
		      }
		    },
		    scrollTouchend:function(e){
				
		      let d = this;
			  // console.log(d.endy)
			  // console.log(d.starty)
			  // console.log(d.scrollindex)
			  // console.log(this.totalnum)
		      if(d.endy-d.starty >100 && d.scrollindex>0){
				  self_.scrollindex=d.scrollindex-1
				  self_.transform = 'translateY(-' + self_.scrollindex*100 + '%)'
		        
		      }else if(d.endy-d.starty <-100 && d.scrollindex<this.totalnum-1 && d.endy!==0){
				   self_.scrollindex=d.scrollindex+1
				   self_.transform = 'translateY(-' + self_.scrollindex*100 + '%)'
		      }
			  self_.starty=0
			  self_.endy=0
			  self_.margintop=0
		    },
		 
	  },
	  onLoad() {
	  	self_=this
	  }
	}
